.message {
    display: flex;
    align-items: center;
    position: relative;
    width: fit-content;
    justify-content: space-between;
    margin: 15px;
}

.message > p {
    background-color: #f3f3f5;
    font-size: medium;
    padding: 15px;
    border-radius: 20px;
    margin: 10px;
    margin-right: auto;
}

/* .chat_name {
    color: gray;
    position: absolute;
    font-size: 8px;
} */

.message > small {
    color: gray;
    position: absolute;
    font-size: 8px;
    bottom: -5px;
    right: 0;
}

.message_sender {
    margin-left: auto;
}

.message_sender > .message_photo {
    order: 1;
    margin: 15px;
}

.message_photo {
    order: 0;
}

.message_sender > p {
    background-color: #3cabfa;
    color: white;
}

/* Hide scrollbar for Chrome, Safari and Opera*/
.chat_messages::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox*/
.chat_messages {
    -ms-overflow-style: none;
    scrollbar-width: none;   
}